*{
    box-sizing: border-box;
}

.content{    
    background: rgb(49,72,227);
    background: linear-gradient(-120deg, rgba(49,72,227,0.95) 0%, rgba(28,163,220,1) 10%, rgba(29,158,220,1) 90%, rgba(49,72,227,0.95) 100%);
    box-shadow: 0px 11px 41px -2px rgba(0,0,0,0.4);
    width: 100%;
    height: 100px;
    margin-bottom: -10px;
}

.header-menu{
    width: 40px;
    height: 40px;
    margin: 30px;
    position: absolute;
}

.header-logo{
    width: 65px;
    height: 65px;
    background: white;
    float: right;
    margin: 18px;
    border-radius: 50px;
}
   
.submenu{
    margin-top: 30px;
    margin-bottom: -20px;
    width: 100%;
}

.submenu-content{    
    background: lightgray;
    width: 100%;
    height: 40px; 
    border-radius: 8px;  
}

.option-task{    
    list-style: none; 
    position: absolute;
    margin-top: 10px;
    margin-left: -20px;   
}

.type-task{       
    display: inline;
    padding-left: 5px;
    margin-right: 5px;
    text-align: center;
}

.type-task.pending{
    background: #1ca3dc;
    text-decoration: none;
    color: white;
    font-size: 12pt;
    padding: 7px;
    border-radius: 8px;
    width: 20%;
    cursor: pointer;
}

.type-task.in-progress{
    background: #31A894;
    text-decoration: none;
    color: white;
    font-size: 12pt;
    padding: 7px;
    border-radius: 8px;
    width: 20%;
    cursor: pointer;
}

.type-task.finish{
    background: #8484b7;
    text-decoration: none;
    color: white;
    font-size: 12pt;
    padding: 7px;
    border-radius: 8px;
    width: 20%;
    cursor: pointer;
}


@media screen and (max-width: 360px){
    .option-task{
        margin-left: -9px;
    } 
    .type-task.pending, .type-task.in-progress, .type-task.finish{
        font-size: 10pt;
    }
}

@media screen and (min-width: 360px)and (max-width: 360px){
    .option-task{
        margin-left: -10px;
    } 

    .type-task{
        margin-left: 2px;
    }
}

@media screen and (min-width: 450px)and (max-width: 540px){
    .option-task{
        margin-left: 4px;
        
    } 

    .type-task{
        margin-left: 2px;
    }

    .type-task.pending, .type-task.in-progress, .type-task.finish{
        font-size: 13pt;
    }
}

@media screen and (min-width: 540px) and (max-width: 640px){
    .submenu-content{
        height: 43px;
    }

    .option-task{
        margin-left: 40px;
    } 

    .type-task{
        margin-left: 2px;
    }

    .type-task.pending, .type-task.in-progress, .type-task.finish{
        font-size: 14pt;
    } 
}

@media screen and (min-width: 640px) and (max-width: 720px){ 

    .option-task{
        margin-left: 110px;
    } 

    .type-task{
        margin-left: 2px;
    }
    
}

@media screen and (min-width: 720px) and (max-width: 800px){
    .submenu-content{
        height: 43px;
    }
    .option-task{
        margin-left: 130px;
    } 

    .type-task{
        margin-left: 2px;
    }

    .type-task.pending, .type-task.in-progress, .type-task.finish{
        font-size: 14pt;
    } 
}

@media screen and (min-width: 800px) and (max-width: 960px){
    .option-task{
        margin-left: 250px;
    } 

    .type-task{
        margin-left: 2px;
    }

}

@media screen and (min-width: 960px) and (max-width: 1600px){
    .submenu-content{
        height: 45px;
    }
    .option-task{
        margin-left: 400px;
    } 

    .type-task{
        margin-left: 3px;
    }

    .type-task.pending, .type-task.in-progress, .type-task.finish{
        font-size: 16pt;
    } 
}


.container-search{
    margin-top: 30px;
    margin-left: 5px;
    margin-right: 5px;
}

.content-search{
    position: relative;
    width: 100%;
    background:  rgb(28,163,220);
    border-radius: .5rem;
}

input, button{
    height: 2rem;
    border: 0;
    color: #2f2f2f;
    font-size: .8rem;
}

input[type=search] {
    outline: 0;
    width: 100%;
    background: #fff;
    padding: 0 1.6rem;
    border-radius: .18rem;
    -webkit-appearance: none;
         appearance: none;
    transition: all 0.5s cubic-bezier(0, 0, 0.43, 1.49);
    transition-property: width, border-radius;
    z-index: 1;
    position: relative;
    box-shadow: 0px 11px 41px -2px rgba(0,0,0,0.4);
}

button {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    width: 3rem;
    background: rgb(28,163,220);
    border-radius:0 .2rem .2rem 0;
}

.icon-search{
    width: 20px;
}

input:not(:-ms-input-placeholder) {
    border-radius: .2rem 0 0 .2rem;
    width: calc(100% - 3rem);
}

input:not(:placeholder-shown) {
    border-radius: .2rem 0 0 .2rem;
    width: calc(100% - 3rem)
}

input:not(:-ms-input-placeholder) + button {
    display: block;
}

input:not(:placeholder-shown) + button {
    display: block;
}






*{
    font-family: 'Mulish', sans-serif; 
    box-sizing: border-box;  
}

.card-container.pending{    
    margin-top: 50px;  
    margin-left: 10px;
    margin-right: 10px;  
    background-color: rgb(28,163,220);    
    border-radius: 1rem;
    margin-bottom: -25px;
    width: 97%;
    height: 150px;
}
.card-content-tags{
    margin-top: -35px;
    margin-bottom: -45px;
    position: absolute;
    margin-left: -25px;
}

.card-content-tags ul li{
    margin-right: 5px;
    display: -webkit-inline-flex;
    display: inline-flex; 
    position: relative;    
    background-color: blue;
    border-top-left-radius: 25px;
    border-bottom-right-radius: 25px;
    width: 20px;
    height: 40px;
    z-index: 10;   
}

.card-img-user{
    margin: 1px;  
    margin-top: 37px;
    margin-left: 15px; 
    padding: 8px;
    border-radius: 5rem;
    background-color: white;     
    position: relative;
    float: left;
    width: 70px;
    height: 70px;
}

.card-content{
    margin-left: 95px;
    margin-bottom: 20px;
    padding-top: 2px;
    width: 55%;  
    position: static;      
}

.card-content h4{
    margin-bottom: -10px;   
    padding-top: 2px;
    color:rgb(214, 211, 211);
}

.card-content h2{
    margin-bottom: 5px;
    color: white;
    font-size: 14pt;
    width: 80%;
}

.card-content #date{
    border: none;
    background: none;
    color: white;
    margin-bottom: 70px;
    font-size: 10.5pt;
}

.card-content-left{
    margin: 10px;
    width: 10%;
    float: right;
    position: relative; 
    
}

.card-menu{
    width: 30px;
    height: 30px;
    float: right; 
    margin-left: 93px; 
    margin-bottom: -20px;   
}

.user_responsable, .user_owner{    
    width: 30px;
    height: 30px;
    padding: 5px; 
    display: block;
    float: right;
    position: relative;
}

.user_responsable{
    margin-bottom: 5px;
}

.user_responsable h3, .user_owner h3{
    font-size: 12.5pt;
    margin-left: -10px;
    padding: 2px;
    background-color: white;
    text-align: center;
    color: #1CA3DC;   
    border-radius: 5px;
    position: absolute;  
    width: 30px;
    height: 30px;
}

.user_responsable h3{      
    color:rgb(11, 11, 165);     
}

.card-content-progressbar{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: -60px;
}

.card-content-progressbar #task{
    width: 100%;
    -webkit-appearance: none;
            appearance: none;
    border: none;
    overflow: hidden;    
    height: 9px; 
    border-radius: 50px;  
}

progress::-webkit-progress-value {
    background: #ffffff;
    border-radius : 20px;  
}
 
progress::-webkit-progress-bar {
    background-color:#D8D8D8;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset; /*esta sombra es para simular el borde que he aplicado arriba para Mozilla*/
    border-radius : 20px ;         
}

/* Responsive menor 300px*/
@media screen and (max-width: 360px){
    .card-container{
        margin-left: 5px;
    }
    .card-img-user{
        width: 55px;
        height: 55px;
    } 
    .card-content #date{
        margin-bottom: 50px !important;
    } 
}  

@media screen and (min-width: 360px)and (max-width: 450px){
    .card-container{
        margin-left: 5px;
    }
    .card-img-user{
        width: 65px;
        height: 65px;
    } 
    .card-content #date{
        margin-bottom: 60px !important;
    } 

}

/* Responsive menor  450px*/
@media screen and (min-width: 450px)and (max-width: 540px){
    .card-container{
        margin-left: 5px;
    }

    .card-img-user{
        width: 67px;
        height: 67px;
    }

    .card-content{
        margin-top: 25px;
        margin-bottom: 15px;
        padding-top: 12px;
    }

    .card-content h4{
        font-size: 11pt;
    }
    
    .card-content h2{
        font-size: 13pt;
    }

    .card-content #date{
        margin-bottom: 8px;
    }

    .card-menu{
        width: 30px;
        height: 30px;
        margin-right: 5px;  
        margin-bottom: -18px;         
    }
    .user_responsable, .user_owner{
        margin-right: 5px;
    }

    .user_responsable h3, .user_owner h3{
       margin-top: 20px;       
       font-size: 11.5pt !important;
       margin-left: -5px;
    }
    

    .card-content-progressbar #task{
        margin-top: 16px; 
    }
    
}

/* Responsive mayor a 450px  */
@media screen and (min-width: 540px) and (max-width: 640px){
    .card-container{
        margin-right: 5px;
        margin-left: 15px;
    }

    .card-img-user{
        width: 73px;
        height: 73px;
    }

    .card-content{
        margin-left: 100px;
    }
    
    .card-menu{
        margin-right: 4.5px;               
    }

    .user_responsable, .user_owner{       
        width: 40%;
        margin-top: .3mm;        
        margin-right: 7px;
        padding-right: 22px;        
    }

}
/* Responsive mayor a 715px  */
@media screen and (min-width: 715px){
    .user_responsable, .user_owner{       
        width: 2%;
        margin-top: .3mm;        
        margin-right: 7px;
        margin-left: 45px;
        padding-right: 22px;
              
    }
}


